/*
@family: имя шрифта (обязательно).
@filename: название шрифта, так же используется в качестве id для шрифта в формате svg (обязательно, по умолчанию используется имя шрифта).
@weight: жирность шрифта (не обязательно, по умолчанию normal).
@style: стиль шрифта (не обязательно, по умолчанию normal).

Пример использования:

.font-face(Roboto Slab, RobotoSlab-Bold, bold);
*/

@font-face {
  font-family: PTSansRegular;
  src: url('../fonts/PTSansRegular/PTSansRegular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: PTSansBold;
  src: url('../fonts/PTSansBold/PTSansBold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  z-index: 1;
  max-width: 1920px;
  margin: 0 auto;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header {
  max-width: 1350px;
  padding-left: 45px;
  padding-right: 45px;
  margin: 0 auto;
}

.header-mobile {
  display: none;
  flex-wrap: wrap;
  flex-direction: row;
}

.header-mobile .hamburger {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 54px;
  height: 40px;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
}

.header-mobile .hamburger:focus {
  outline: none;
}

.header-mobile .hamburger span {
  display: block;
  position: absolute;
  top: 20px;
  left: 18px;
  right: 18px;
  height: 3px;
  background: white;
}

.header-mobile .hamburger span::before,
.header-mobile .hamburger span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  content: "";
}

.header-mobile .hamburger span::before {
  top: -8px;
}

.header-mobile .hamburger span::after {
  bottom: -8px;
}

.header-mobile .hamburger--rot {
  background-color: #28aadc;
}

.header-mobile .hamburger--rot span {
  transition: transform 0.3s;
}

.header-mobile .hamburger--rot.is-active {
  background-color: #166888;
}

.header-mobile .hamburger--rot.is-active span {
  transform: rotate(90deg);
}

.header-mobile-nav {
  flex-grow: 1;
  background-color: #f0f1f6;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.header-mobile-nav .fa {
  font-size: 20px;
  color: #dbdbdc;
}

.header-main {
  margin-top: 35px;
}

.header-main-wrapper {
  max-width: 1220px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}

.header-main-wrapper-logoline {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.header-main-wrapper-logoline-logo {
  margin-right: 43px;
  padding-bottom: 10px;
}

.header-main-wrapper-logoline-logo p {
  color: #747688;
  font-family: PTSansRegular, sans-serif;
  font-size: 14px;
}

.header-main-wrapper-logoline-search {
  flex: 0 1 40%;
  position: relative;
  margin-top: -5px;
}

.header-main-wrapper-logoline-search .search {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid #b9b8bd;
  box-sizing: border-box;
  padding: 15px 30px 12px 10px;
  color: #b9b8bd;
  font-family: PTSansRegular, sans-serif;
  font-size: 14px;
}

.header-main-wrapper-logoline-search .fa-search {
  font-size: 18px;
  color: #cb944c;
  position: absolute;
  right: 0;
  top: 0;
  padding: 10px 9px 12px 0;
  cursor: pointer;
}

.header-main-wrapper-logoline-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-end;
  flex: 1 1 100px;
}

.header-main-wrapper-logoline-info p {
  color: #181616;
  font-family: PTSansRegular, sans-serif;
  font-size: 23px;
}

.header-main-wrapper-logoline-info .info {
  margin-left: 16px;
}

.header-main-wrapper-logoline-info .info-days {
  color: #cb944c;
  font-family: PTSansRegular, sans-serif;
  font-size: 14px;
}

.header-main-wrapper-logoline-info .info-delivery {
  color: #2b3056;
  font-family: PTSansRegular, sans-serif;
  font-size: 14px;
}

.header-main-wrapper hr {
  border: none;
  height: 1px;
  background-color: #dddfdf;
}

.header-main-wrapper-nav {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 14px;
  margin-bottom: 14px;
}

.header-main-wrapper-nav nav {
  min-width: 810px;
}

.header-main-wrapper-nav nav .menu {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  flex: 1 1 800px;
}

.header-main-wrapper-nav nav .menu li {
  color: #181818;
  font-family: PTSansBold, sans-serif;
  font-size: 16px;
}

.header-main-wrapper-nav nav .menu li a {
  color: #181818;
  font-family: PTSansBold, sans-serif;
  font-size: 16px;
  text-decoration: none;
}

.header-main-wrapper-nav nav .menu-li {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  cursor: pointer;
  position: relative;
}

.header-main-wrapper-nav nav .menu-li-drop-menu {
  display: none;
  border: 1px solid #dddfdf;
  box-shadow: 2px 3px 1px rgba(108, 104, 104, 0.33);
  border-radius: 3px;
  width: 245px;
  background-color: #fff;
  position: absolute;
  box-sizing: border-box;
  z-index: 10;
  top: 50px;
  left: -15px;
  overflow: block;
}

.header-main-wrapper-nav nav .menu-li-drop-menu:nth-last-child(n) {
  padding-bottom: 20px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .fa-circle-o {
  font-size: 8px;
  color: #cb944c;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .first,
.header-main-wrapper-nav nav .menu-li-drop-menu .second {
  margin-top: 10px;
  position: relative;
  color: #666666;
  font-family: PTSansRegular, sans-serif;
  font-size: 15px;
  line-height: 19px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .first .fa-angle-right,
.header-main-wrapper-nav nav .menu-li-drop-menu .second .fa-angle-right {
  position: absolute;
  right: 0;
  padding-right: 15px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .first:hover .first-drop,
.header-main-wrapper-nav nav .menu-li-drop-menu .second:hover .first-drop {
  display: block;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .second {
  margin-top: 10px;
  position: relative;
}

.header-main-wrapper-nav nav .menu-li-drop-menu .second:hover .second-drop {
  display: block;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li {
  box-sizing: border-box;
  padding: 5px 15px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li:hover {
  background-color: #e3e3e4;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li span {
  padding-right: 10px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li .fa-angle-right {
  font-size: 20px;
  color: #cb944c;
  align-self: center;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li .first-drop,
.header-main-wrapper-nav nav .menu-li-drop-menu li .second-drop {
  display: none;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li a {
  width: 85%;
  color: #666666;
  font-family: PTSansRegular, sans-serif;
  font-size: 15px;
  line-height: 19px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li .first-drop,
.header-main-wrapper-nav nav .menu-li-drop-menu li .second-drop {
  position: absolute;
  left: 247px;
  top: -15px;
  width: 245px;
  z-index: 1111;
  box-shadow: 2px 3px 1px rgba(108, 104, 104, 0.33);
  border-radius: 3px;
  border: 1px solid #dddfdf;
  background-color: #ffffff;
  padding: 23px 63px 30px 18px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li .first-drop h3,
.header-main-wrapper-nav nav .menu-li-drop-menu li .second-drop h3 {
  z-index: 11;
  color: #cb944c;
  font-family: PTSansBold, sans-serif;
  font-size: 16px;
  margin-bottom: 10px;
}

.header-main-wrapper-nav nav .menu-li-drop-menu li .first-drop li,
.header-main-wrapper-nav nav .menu-li-drop-menu li .second-drop li {
  padding-left: 2px;
}

.header-main-wrapper-nav nav .menu-li .fa-angle-down {
  font-size: 20px;
  color: #cb944c;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 20px;
  height: 20px;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  background-color: #fff;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-toggle-menu {
  display: block;
  position: absolute;
  top: 9px;
  left: 4px;
  right: 4px;
  height: 3px;
  background: #cb944c;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-toggle-menu::before,
.header-main-wrapper-nav nav .menu-li .c-hamburger-toggle-menu::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #cb944c;
  content: "";
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-toggle-menu::before {
  top: -5px;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-toggle-menu::after {
  bottom: -5px;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-half {
  display: block;
  position: absolute;
  top: 9px;
  left: 0;
  right: 4px;
  height: 3px;
  width: 15%;
  background: #cb944c;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-half::before,
.header-main-wrapper-nav nav .menu-li .c-hamburger-half::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #cb944c;
  content: "";
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-half::before {
  top: -5px;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger-half::after {
  bottom: -5px;
}

.header-main-wrapper-nav nav .menu-li .c-hamburger:focus {
  outline: none;
}

.header-main-wrapper-nav-loggin {
  margin-left: 98px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex-grow: 1;
  justify-content: flex-end;
}

.header-main-wrapper-nav-loggin-count {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 70%;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
}

.header-main-wrapper-nav-loggin-count .fa-shopping-cart {
  font-size: 20px;
  color: #cb944c;
  cursor: pointer;
}

.header-main-wrapper-nav-loggin-count-sum {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  padding: 0;
  border-radius: 100%;
  background-color: #cb944c;
  color: #fff;
  font-size: 14px;
}

.header-main-wrapper-nav-loggin-count-text,
.header-main-wrapper-nav-loggin-count-price {
  color: #252a54;
  font-family: PTSansBold, sans-serif;
  font-size: 16px;
}

.header-main-wrapper-nav-loggin-singup {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 25%;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.header-main-wrapper-nav-loggin-singup img {
  width: 19px;
  height: 17px;
}

.header-main-wrapper-nav-loggin-singup p {
  color: #252a54;
  font-family: PTSansBold, sans-serif;
  font-size: 16px;
}

.header-main-wrapper-loggin-md {
  display: none;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin-top: 5px;
}

.header-main-wrapper-loggin-md-count {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 140px;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
}

.header-main-wrapper-loggin-md-count .fa-shopping-cart {
  font-size: 18px;
  color: #cb944c;
  cursor: pointer;
}

.header-main-wrapper-loggin-md-count-sum {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  padding: 0;
  border-radius: 100%;
  background-color: #cb944c;
  color: #fff;
  font-size: 10px;
}

.header-main-wrapper-loggin-md-count-text,
.header-main-wrapper-loggin-md-count-price {
  color: #252a54;
  font-family: PTSansBold, sans-serif;
  font-size: 10px;
}

.header-main-wrapper-loggin-md-singup {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 65px;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.header-main-wrapper-loggin-md-singup img {
  width: 17px;
  height: 15px;
}

.header-main-wrapper-loggin-md-singup p {
  color: #252a54;
  font-family: PTSansBold, sans-serif;
  font-size: 13px;
}

.header-slider {
  max-width: 1350px;
  padding-left: 45px;
  padding-right: 45px;
  margin: 0 auto;
  position: relative;
  margin-top: 36px;
  background-image: url('../img/slick-bg/thumb_16050_blog_full.png');
}

.header-slider-h {
  position: relative;
  min-height: 280px;
}

.header-slider-h-h1 h1 {
  color: #ffffff;
  max-width: 430px;
  background-color: rgba(210, 166, 107, 0.88);
  font-family: PTSansRegular, sans-serif;
  font-size: 43px;
  line-height: 47.66px;
  position: absolute;
  padding: 20px 48px 20px 33px;
  top: 27%;
  left: 5%;
}

.prev,
.next {
  position: absolute;
  top: 27%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  color: transparent;
  border: none;
  outline: none;
  background-color: rgba(210, 166, 107, 0.88);
  cursor: pointer;
  width: 37px;
  height: 128px;
}

.prev .fa-angle-left,
.next .fa-angle-left,
.prev .fa-angle-right,
.next .fa-angle-right {
  font-size: 40px;
}

.prev:hover,
.next:hover {
  background-color: rgba(255, 255, 255, 0.88);
}

.prev:hover .fa-angle-left,
.next:hover .fa-angle-left,
.prev:hover .fa-angle-right,
.next:hover .fa-angle-right {
  color: #cb944c;
}

.f-prev,
.f-next {
  color: #fff;
  font-size: 16px;
}

.prev {
  left: 0;
  z-index: 1;
}

.next {
  right: 0;
  z-index: 1;
}

/* dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.header-dots {
  position: absolute;
  bottom: -60px;
  left: 2px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.header-dots li {
  position: relative;
  display: inline-block;
  width: 10px;
  height: 10px;
  padding: 12px;
  cursor: pointer;
}

.header-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
  padding: 0px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.header-dots li button:hover,
.header-dots li button:focus {
  outline: none;
}

.header-dots li button:hover:before,
.header-dots li button:focus:before {
  opacity: 1;
}

.header-dots li button:before {
  font-family: 'slick';
  font-size: 16px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 5px;
  content: '•';
  text-align: center;
  color: #cb944c;
  opacity: 0.3;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header-dots li.slick-active button:before {
  opacity: 1;
  color: #cb944c;
}

@media screen and (max-width: 1350px) {
  .header-main-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .header-main-wrapper-logoline-logo {
    margin-right: 22px;
  }

  .header-main-wrapper-logoline-info p {
    font-size: 18px;
  }

  .header-main-wrapper-logoline-info .info-days {
    font-size: 12px;
  }

  .header-main-wrapper-logoline-info .info-delivery {
    font-size: 12px;
  }

  .header-main-wrapper-nav nav {
    min-width: 320px;
    width: 100%;
  }

  .header-main-wrapper-nav-loggin {
    display: none;
  }

  .header-main-wrapper-nav-loggin-count-text,
  .header-main-wrapper-nav-loggin-count-price {
    font-size: 10px;
  }

  .header-main-wrapper-nav-loggin-singup p {
    font-size: 13px;
  }

  .header-main-wrapper-loggin-md {
    display: flex;
  }

  .header-slider {
    padding-left: 10px;
    padding-right: 10px;
  }

  .slick-dots {
    top: 220%;
  }
}

@media screen and (max-width: 1050px) {
  .header-main-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .header-main-wrapper {
    width: 100%;
  }

  .header-main-wrapper-logoline-logo {
    width: 178px;
  }

  .header-main-wrapper-logoline-logo p {
    font-size: 12px;
  }

  .header-main-wrapper-logoline-search {
    flex: 0 1 220px;
    margin: 0px;
  }

  .header-main-wrapper-logoline-search .search {
    height: 31px;
    font-size: 10px;
  }

  .header-main-wrapper-logoline-search .fa-search {
    font-size: 13px;
  }

  .header-main-wrapper-logoline-info p {
    font-size: 15px;
  }

  .header-main-wrapper-logoline-info .info-days {
    font-size: 10px;
  }

  .header-main-wrapper-nav nav .menu li {
    font-size: 13px;
  }

  .header-main-wrapper-nav nav .menu li a {
    font-size: 13px;
  }

  .header-main-wrapper-nav nav .menu-li {
    width: 150px;
  }

  .header-main-wrapper-nav nav .menu-li-drop-menu .first,
  .header-main-wrapper-nav nav .menu-li-drop-menu .second {
    font-size: 13px;
  }

  .header-slider {
    margin-top: 26px;
    padding: 0;
    background-image: url('../img/slick-bg/786.png') no-reapit;
  }

  .header-slider-h {
    min-height: 165px;
  }

  .header-slider-h-h1 h1 {
    font-size: 23px;
    padding: 10px 24px 10px 18px;
    width: 250px;
    line-height: 23px;
  }

  .prev,
  .next {
    width: 21px;
    height: 65px;
  }

  .prev .fa-angle-left,
  .next .fa-angle-left,
  .prev .fa-angle-right,
  .next .fa-angle-right {
    font-size: 30px;
  }

  .header-dots {
    bottom: -40px;
  }

  .header-dots li {
    padding: 8px;
  }

  .header-dots li button:before {
    font-size: 13px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding-left: 10px;
    padding-right: 10px;
  }

  .header-mobile {
    display: flex;
  }

  .header-main-wrapper {
